er-color);\n\n    input {\n        opacity: 0;\n        height: 100%;\n        width: 100%;\n    }\n\n    &:focus-within {\n        outline: none;\n        box-shadow: ag-param(input-focus-box-shadow);\n    }\n\n    &.ag-disabled {\n        opacity: 0.5;\n    }\n\n    &.ag-checked {\n        @include ag-color-property(background-color, toggle-button-on-background-color);\n        @include ag-color-property(border-color, toggle-button-on-border-color);\n    }\n\n    &::before {\n        content: ' ';\n        position: absolute;\n        top: -$border-width;\n        left: -$border-width;\n        display: block;\n        box-sizing: border-box;\n        height: ag-param(toggle-button-height);\n        width: ag-param(toggle-button-height);\n        @include ag-color-property(background-color, toggle-button-switch-background-color);\n        border-radius: ag-param(toggle-button-height) / 2;\n        transition: left 100ms;\n        border: $border-width solid;\n        @include ag-color-property(border-color, toggle-button-switch-border-color);\n    }\n\n    &.ag-checked::before {\n        left: calc(100% - #{ag-param(toggle-button-height)} );\n        @include ag-color-property(border-color, toggle-button-on-border-color);\n    }\n}\n\n@mixin ag-internal-range-track {\n    @if mixin-exists(ag-range-track) {\n        @include ag-range-track\n    }\n    @else {\n        margin: 0;\n        padding: 0;\n        width: 100%;\n        height: 3px;\n        @include ag-color-property(background-color, border-color);\n        border-radius: ag-param(border-radius);\n        border-radius: ag-param(checkbox-border-radius);\n    }\n}\n\n@mixin ag-internal-focus-range-track {\n    @if mixin-exists(ag-focus-range-track) {\n        @include ag-focus-range-track\n    }\n}\n\n@mixin ag-internal-active-range-track {\n    @if mixin-exists(ag-focus-range-track) {\n        @include ag-focus-range-track\n    }\n    @else {\n        @include ag-color-property(background-color, input-focus-border-color);\n    }\n}\n\n@mixin ag-internal-range-thumb {\n    @if mixin-exists(ag-range-thumb) {\n        @include ag-range-thumb\n    }\n    @else {\n        margin: 0;\n        padding: 0;\n        -webkit-appearance: none;\n        width: ag-param(icon-size);\n        height: ag-param(icon-size);\n        @include ag-color-property(background-color, background-color);\n        border: 1px solid;\n        @include ag-color-property(border-color, checkbox-unchecked-color);\n        border-radius: ag-param(icon-size);\n    }\n}\n\n@mixin ag-internal-focus-range-thumb {\n    box-shadow: ag-param(input-focus-box-shadow);\n    @if mixin-exists(ag-focus-range-thumb) {\n        @include ag-focus-range-thumb\n    }\n    @else {\n        @include ag-color-property(border-color, checkbox-checked-color);\n    }\n}\n\n@mixin ag-internal-active-range-thumb {\n    @if mixin-exists(ag-active-range-thumb) {\n        @include ag-active-range-thumb\n    }\n}\n\n@mixin ag-range {\n    -webkit-appearance: none;\n    width: 100%;\n    height: 100%;\n    background: none;\n    overflow: visible;\n\n    &::-webkit-slider-runnable-track {\n        @include ag-internal-range-track;\n    }\n\n    &::-moz-range-track {\n        @include ag-internal-range-track;\n    }\n\n    &::-ms-track {\n        @include ag-internal-range-track;\n        color: transparent;\n        width: calc(100% - 2px);\n    }\n\n    &::-webkit-slider-thumb {\n        @include ag-internal-range-thumb;\n        transform: translateY(ag-param(icon-size) * -0.5 + 1.5px);\n    }\n\n    &::-ms-thumb {\n        @include ag-internal-range-thumb;\n    }\n    &::-moz-ag-range-thumb {\n        @include ag-internal-range-thumb;\n    }\n\n    &:focus {\n        outline: none;\n\n        &::-webkit-slider-runnable-track {\n            @include ag-internal-focus-range-track;\n        }\n        &::-moz-ag-range-track {\n            @include ag-internal-focus-range-track;\n        }\n        &::-ms-track {\n            @include ag-internal-focus-range-track;\n        }\n\n        &::-webkit-slider-thumb {\n            @include ag-internal-focus-range-thumb;\n        }\n        &::-ms-thumb {\n            @include ag-internal-focus-range-thumb;\n        }\n        &::-moz-ag-range-thumb {\n            @include ag-internal-focus-range-thumb;\n        }\n    }\n\n    &:active {\n        &::-webkit-slider-runnable-track {\n            @include ag-internal-active-range-track;\n        }\n        &::-moz-ag-range-track {\n            @include ag-internal-active-range-track;\n        }\n        &::-ms-track {\n            @include ag-internal-active-range-track;\n        }\n\n        &::-webkit-slider-thumb {\n            @include ag-internal-active-range-thumb;\n        }\n        &::-ms-thumb {\n            @include ag-internal-active-range-thumb;\n        }\n        &::-moz-ag-range-thumb {\n            @include ag-internal-active-range-thumb;\n        }\n    }\n\n    &:disabled {\n        opacity: 0.5;\n    }\n}\n","\n@mixin ag-theme-base-part-grid-layout {\n\n    .ag-rtl {\n        text-align: right;\n    }\n\n    .ag-root-wrapper {\n        @include ag-border(primary, $radius: ag-param(border-radius));\n    }\n    \n    // support 20 levels here because row group indentation is used for tree data which can be quite deep\n    @for $i from 1 to 20 {\n        .ag-row > .ag-cell-wrapper.ag-row-group-indent-#{$i} {\n            @include ag-theme-rtl(( padding-left: ag-param(cell-horizontal-padding) + $i * ag-param(row-group-indent-size) ));\n        }\n        \n        .ag-row-group-indent-#{$i} {\n            @include ag-theme-rtl(( padding-left: $i * ag-param(row-group-indent-size) ));\n        }\n\n        .ag-row-level-#{$i} .ag-row-group-leaf-indent {\n            @include ag-theme-rtl(( margin-left: ag-param(row-group-indent-size) ));\n        }\n    }\n\n    .ag-cell {\n        -webkit-font-smoothing: subpixel-antialiased;\n    }\n\n    .ag-value-change-delta {\n        padding-right: 2px;\n    }\n\n    .ag-value-change-delta-up {\n        @include ag-color-property(color, value-change-delta-up-color);\n    }\n\n    .ag-value-change-delta-down {\n        @include ag-color-property(color, value-change-delta-down-color);\n    }\n\n    .ag-value-change-value {\n        background-color: transparent;\n        border-radius: 1px;\n        padding-left: 1px;\n        padding-right: 1px;\n        transition: background-color 1s;\n    }\n\n    .ag-value-change-value-highlight {\n        @include ag-color-property(background-color, value-change-value-highlight-background-color);\n        transition: background-color 0.1s;\n    }\n\n    .ag-cell-data-changed {\n        @include ag-color-property(background-color, value-change-value-highlight-background-color, $important: true);\n    }\n\n    .ag-cell-data-changed-animation {\n        background-color: transparent;\n    }\n\n    .ag-cell-highlight {\n        @include ag-color-property(background-color, range-selection-highlight-color, $important: true);\n    }\n\n    .ag-row {\n        height: ag-param(row-height);\n        @include ag-color-property(background-color, background-color);\n        @include ag-color-property(color, data-color);\n        \n        @if ag-param-is-set(row-border-color) and ag-param(borders-critical) {\n            // NOTE: we define border-width and border-color at .ag-row level and only set\n            // border-style inside nested pseudoclass selectors, to allow derived themes\n            // to override border-width on .ag-row\n            border-width: 1px;\n            @include ag-color-property(border-color, row-border-color);\n\n            &:not(.ag-row-first) {\n                border-top-style: solid;\n            }\n            &.ag-row-last {\n                border-bottom-style: solid;\n            }\n        }\n    }\n\n    .ag-row-highlight-above::after, .ag-row-highlight-below::after {\n        content: '';\n        position: absolute;\n        width: calc(100% - 1px);\n        height: 1px;\n        @include ag-color-property(background-color, range-selection-border-color);\n        left: 1px;\n    }\n\n    .ag-row-highlight-above::after {\n        top: -1px;\n    }\n\n    .ag-row-highlight-above.ag-row-first::after {\n        top: 0;\n    }\n\n    .ag-row-highlight-below::after {\n        bottom: 0px;\n    }\n\n    .ag-row-odd {\n        @include ag-color-property(background-color, odd-row-background-color);\n    }\n\n    // NOTE: these don't need an RTL version because the \"left spacer\" is always the one visually on the left\n    .ag-horizontal-left-spacer:not(.ag-scroller-corner) {\n        @include ag-border(critical, right);\n    }\n    .ag-horizontal-right-spacer:not(.ag-scroller-corner) {\n        @include ag-border(critical, left);\n    }\n\n    .ag-row-hover {\n        @if ag-param-is-set(row-hover-color) {\n            @include ag-color-property(background-color, row-hover-color);\n        }\n    }\n\n    .ag-right-aligned-cell {\n        @include ag-theme-rtl(( text-align: right ));\n    }\n\n    // right-align numeric values in cells with wrappers\n    .ag-right-aligned-cell .ag-cell-value {\n        @include ag-theme-rtl(( margin-left: auto ));\n    }\n\n    .ag-cell {\n        // draw a transparent border so that the cell size doesn't change when we add a range border\n        border: 1px solid transparent;\n        // adjust for transparent border\n        line-height: ag-param(row-height) - 2px;\n        padding-left: ag-param(cell-horizontal-padding) - 1px;\n        padding-right: ag-param(cell-horizontal-padding) - 1px;\n    }\n\n    // in full width rows, a cell renderer is rendered directly into a row with no cell in between,\n    // in which case we need to apply the padding to the cell renderer's wrapper.\n    .ag-row > .ag-cell-wrapper {\n        padding-left: ag-param(cell-horizontal-padding) - 1px;\n        padding-right: ag-param(cell-horizontal-padding) - 1px;\n    }\n\n    .ag-row-dragging