, bottom);\n    }\n\n    .ag-filter-apply-panel {\n        padding: ag-param(widget-container-vertical-padding) ag-param(widget-container-horizontal-padding);\n\n        @include ag-border(secondary, top);\n    }\n\n    .ag-filter-apply-panel-button {\n        line-height: 1.5;\n        @include ag-theme-rtl((margin-left: ag-param(grid-size) * 2));\n    }\n\n    .ag-simple-filter-body-wrapper {\n        @include ag-vertical-widget-container();\n    }\n\n    .ag-filter-no-matches {\n        margin: ag-param(widget-container-vertical-padding) ag-param(widget-container-horizontal-padding);\n    }\n}\n","\n@mixin ag-theme-base-part-sidebar {\n \n    .ag-side-bar {\n        position: relative;\n    }\n\n    .ag-tool-panel-wrapper {\n        @include ag-color-property(background-color, control-panel-background-color);\n    }\n\n    .ag-side-buttons {\n        padding-top: ag-param(grid-size) * 4;\n        width: ag-param(icon-size) + 4;\n        position: relative;\n        @include ag-color-property(color, foreground-color);\n        overflow: hidden;\n    }\n\n    button.ag-side-button-button {\n        @include ag-inherit-text-styles();\n        background: transparent;\n        padding: ag-param(grid-size) * 2 0 ag-param(grid-size) * 2 0;\n        @if ag-param(borders-side-button) {\n            width: calc(100% + 1px);\n        } @else {\n            width: 100%;\n        }\n        margin: 0;\n        min-height: ag-param(grid-size) * 18;\n        background-position-y: center;\n        background-position-x: center;\n        background-repeat: no-repeat;\n\n        border: none;\n        @include ag-border(side-button, (top, bottom));\n        $border-top-color: transparent;\n        $border-bottom-color: transparent;\n\n        &:focus {\n            box-shadow: none;\n        }\n    }\n\n    @include ag-keyboard-focus((ag-side-button-button), 4px);\n\n    .ag-selected .ag-side-button-button {\n        @if ag-param(borders-side-button) {\n            @include ag-c